//------------------------------------//
//  $BUTTONS
//------------------------------------//

.btn {
  background: $primary;
  display: inline-block;
  margin: 0;
  padding: .4em 1em;
  color: $white;
  font-size: 1rem;
  line-height: 1.6em;
  text-align: center;
  text-transform: uppercase;
  vertical-align: middle;
  white-space: nowrap;
  border: 0;
  border-radius: 0;
  cursor: pointer;

  &,
  &:hover,
  &:focus { text-decoration: none; }

  &.disabled,
  &[disabled] {
    cursor: not-allowed;
    opacity: .65;
  }
}
