.site-header {
  background: $secondary;
  padding: $half-spacing-unit;

  .inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  svg {
    height: 40px;
    width: 40px;
  }

  a {
    color: $white;
    font-family: $font-head-secondary;
    text-transform: uppercase;
  }

  .btn {
    height: 40px;
  }
}